<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="Hiring" SubModuleCode="" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="REQUISITION_ID" :listType="['List']" :ListData="JobData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            
        </tg-list>
        <AddGroup v-if="IsGroupOpen" :HeaderText="HeaderText"></AddGroup>
    </section>
</template>
<script>
import Schema from '../JobPost/Schema';
import DataService from '../../../services/DataService';
import moment from 'moment'
export default {
    data() {
        return {
            isLoading: false,
            IsRequisitionOpen: false,
            Headers: [],
            leftSearchFields: [{
                fieldName: this.$t('CASearchbyDesignationName'),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            requisitionid: 0,
            userType: '',
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,           
            searchCondition:'',
            JobData: [],
            search: '',
            listheaderbuttons: Schema.RequisitionButtons,
            listActions: Schema.RequisitionActions,
            CompanyId: null,
            Userid: null,
            HeaderText: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        await vm.FetchData();
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('JobRequisition');

            divElement[0].appendChild(spanElement);
        }
    },
    mounted() {
        var ths = this;
        ths.leftSearchFields.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case "EditRequisition":
                    this.AddRequisition(item.REQUISITION_ID, 'EditRequisition');
                    break;
                case "ViewRequisition":
                    this.ViewRequisition(item.REQUISITION_ID);
                    break;
                case "ApproveRequisition":
                    this.ApproveRequisition(item.REQUISITION_ID);
                    break;
                case "RejectRequisition":
                    this.RejectRequisition(item.REQUISITION_ID);
                    break;
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddRequisition":
                    this.AddRequisition(0, 'AddRequisition');
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Approve':
                        action.isVisible = true;
                        break;
                    case 'Reject':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `userType=${vueObj.userType}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}` 
            if (!vueObj.isBlank(this.searchCondition)) {
                url += `&search=${this.searchCondition}`;
            } 
            await DataService.RequisitionList(url).then((response) => {
                if (response.data && response.data.data.length > 0) {
                    vueObj.isLoading = false;
                    vueObj.Headers = response.data.schema;
                    vueObj.JobData = response.data.data;
                    vueObj.JobData.forEach((item) => {
                        if (item.created_at) {
                            item.created_at= vueObj.formatTimestamp(item.created_at)
                        }
                    })
                    vueObj.TotalRecords = response.data.data[0].totalrecords;
                    vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;
                    vueObj.listheaderbuttons[1].isdisabled = true;
                    vueObj.noRecords = false;
                } else {
                    vueObj.isLoading = false;
                    vueObj.JobData = [];
                    vueObj.TotalRecords = 0
                    vueObj.noRecords = true;
                }
            })
        },
        formatTimestamp: function (input) {                        
            const date = moment(input);
            const formattedDate = date.format('MM/DD/YYYY h:mm A');
            return formattedDate;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Requisition")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.CompanyId,
                        userId: vm.Userid,
                        moduleName: "",
                        subModuleCode: "",
                        refCode:"DELETE_REQUISITION"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Requisition')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Requisition')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        AddRequisition: function (requisitionid, TextHeader) {
            this.requisitionid = requisitionid;
            this.HeaderText = TextHeader;
            this.IsRequisitionOpen = true;
            if(TextHeader=='AddRequisition')
                this.$router.push({ name: 'AddRequisition', params: { reqfrom: 'JobPost/AddRequisition', requisitionId: requisitionid} });
            else
                this.$router.push({ name: 'EditRequisition', params: { reqfrom: 'JobPost/EditRequisition', requisitionId: requisitionid} });
        },
        ViewRequisition: function (requisitionid) {   
            this.$router.push({ name: 'JobRequisitionDetail', params: { reqfrom: 'JobPost/RequisitionDetail', requisitionId: requisitionid} });
        }
    }
}
</script>
.